import React, { Component } from 'react';
import {Glyphicon, Button} from 'react-bootstrap';

class Todos extends Component {

    removeTodo(id){
        this.props.onDelete(id);

    }
  render() {
    return (
        <div className="row">
            { this.props.todos.map((todo) => 
                <div className="card text-white bg-info col-md-6" key={todo.id}>
                    <div className="card-header">
                        {todo.title }  
                        <span className="badge badge-dark badge-pill">{todo.priority}</span>
                    </div>
                    <div className="card-body">
                        <p><Glyphicon glyph="user"></Glyphicon> {todo.responsible}</p>
                        <p>{todo.description}</p>
                        <Button bsStyle="primary" bsSize="small" onClick={this.removeTodo.bind(this, todo.id)}><Glyphicon glyph="trash"></Glyphicon> Delete</Button>
                    </div>
                </div>
            )}
        </div>
    );
  }
}

export default Todos;



